import React from 'react'
import { DomainsResultsStyled, ColorsStyled } from './DomainsResults.styled'
import Options from './Options'
import Tlds from './Tlds'
import Doms from './Doms'
import { LinkToToggleOptionsStyled } from './Options.styled'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'
import { faAngleDown, faAngleUp } from '@fortawesome/pro-regular-svg-icons'
import { LegendStyled } from './Legend.styled'

const DomainsResults = function(props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * View variables:
   */
  let { suggestions_phrase_lists, suggestions_phrases } = that.props

  return (
    <>
      {/*
       * Options
       */}
      <Options that={that} />

      {/*
       * Show WIP
       */}
      <LinkToToggleOptionsStyled
        className="LinkToToggleOptionsStyled onDomainsResults"
        onClick={that.toggleWip}
      >
        {that.state.showWip ? (
          <>
            <span className="hide-small">hide&nbsp;</span>WIP{' '}
            <FA icon={faAngleUp} className="faAngleUp" />
          </>
        ) : (
          <>
            <span className="hide-small">show&nbsp;</span>WIP{' '}
            <FA icon={faAngleDown} className="faAngleDown" />
          </>
        )}
      </LinkToToggleOptionsStyled>

      {/*
       * Suggestions + TLDs + WIP
       */}
      <DomainsResultsStyled>
        <div className="content">
          <ColorsStyled
            className={
              'results ' +
              (!that.state.gotAvailability
                ? ' gettingAvailability'
                : ' gotAvailability')
            }
          >

            {/*
             * WIP
             */}
            {!!that.state.showWip && (
              <div className="wip doms columns">

                {/*
                 * PHRASES (debugging, temporary)
                 */}
                <div className="column">
                  <div className="nowrap">
                    <b>results</b>
                  </div>
                  <div className="pre">
                    {suggestions_phrases.map((phrase, pi) => (
                      <div key={pi}>&quot;{phrase.words.join(' ')}&quot;</div>
                    ))}
                  </div>
                </div>

                {/*
                 * WIP PHRASE LISTS (debugging, temporary)
                 */}
                {Object.keys(suggestions_phrase_lists || {}).map((title, i) => {
                  let lists = suggestions_phrase_lists[title]
                  return (
                    <div key={title + i} className="column">
                      <div className="nowrap">
                        <b>{title}</b>
                      </div>
                      <div>
                        {lists.map((phrase, pi) => (
                          <div key={pi}>&quot;{phrase.join(' ')}&quot;</div>
                        ))}
                      </div>
                    </div>
                  )
                })}
              </div>
            )}

            {/*
              * Results
             */}
            {!that.state.showWip && (
              <>
                <Doms that={that} />
                <Tlds that={that} />
              </>
            )}
          </ColorsStyled>
        </div>
      </DomainsResultsStyled>
    </>
  )
}

export default DomainsResults
