import React from 'react'
import PropTypes from 'prop-types'
import { faCaretDown } from '@fortawesome/pro-solid-svg-icons'
import { faHeart } from '@fortawesome/pro-regular-svg-icons'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'
import { H5 } from './DomainsResults.styled'
import { DomsStyled } from './Doms.styled'

let list_label = function(listname) {
  if (listname === 'original') {
    return 'Exact phrase'
  }
  if (listname === 'domain hacks') {
    return 'Domain hacks + Word hacks'
  }
  if (!listname) {
    return 'Alternative phrases'
  }
  return listname
}
let list_limit = function(listname) {
  if (!listname) {
    return 36
  }
  if (listname === 'original') {
    return 9
  }
  return 6
}

class Doms extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      // ['unlimit_'+listname]: {boolean}
    }
  }

  render() {
    /*
     * User "container" state/props/methods!
     * This "child" component is to simplify container's render() logic.
     */
    let that = this.props.that
    let { suggestions_domains, suggestions_availability } = that.props
    /*
     * Validate
     */
    if (!suggestions_domains || !Object.keys(suggestions_domains).length) {
      return (
        <DomsStyled className="DomsStyled">
          <div className="content">
            <p>No results...</p>
          </div>
        </DomsStyled>
      )
    }
    let ListNames = []
    let Lists = []
    for (let name in suggestions_domains) {
      let list = suggestions_domains[name]
      if (list.length) {
        ListNames.push(name)
        Lists.push(
          list
            .map((phraseString, i) => {
              let code = Number(
                suggestions_availability[phraseString.replace(/ /g, '')] || 0,
              )
              // hide/show depending on the status:
              if (
                code !== 0 && // 0 means "not checked"
                ((code === 1 && that.props.hide_available) ||
                  (code === 2 && that.props.hide_premium) ||
                  (code >= 10 && that.props.hide_unavailable))
              ) {
                // hide - do not render!
              } else {
                // show
                return (
                  <div key={phraseString + i} className={'dom_name status' + code}>
                    <div className={'dom_card'}>
                      <span>
                        {phraseString.split(' ').map((str, i) => (
                          <span key={str + i} className="word">
                            {str}
                          </span>
                        ))}
                      </span>
                      &nbsp;&nbsp;
                      <FA icon={faHeart} />
                    </div>
                  </div>
                )
              }
            })
            .filter(it => !!it),
        )
      }
    }
    return (
      <DomsStyled className="DomsStyled">
        {Lists.map((List, li) => {
          let listname = ListNames[li]
          let label = list_label(listname)
          let ListLimited = this.state['unlimit_' + listname]
            ? List
            : List.slice(0, list_limit(listname))
          return ListLimited.length ? (
            <div key={li} className="doms_group">
              <H5>{label}</H5>
              <div className="doms_content">{ListLimited}</div>
              {List.length > ListLimited.length && (
                <div className="unlimit">
                  <span
                    onClick={() => {
                      this.setState({
                        ['unlimit_' + listname]: true,
                      })
                    }}
                  >
                    more &thinsp;
                    <FA className="fa-angle-down" icon={faCaretDown} />
                  </span>
                </div>
              )}
            </div>
          ) : null
        })}
      </DomsStyled>
    )
  }
}

Doms.propTypes = {
  suggestions_domains: PropTypes.object,
  suggestions_availability: PropTypes.object,
}

export default Doms
