import React from 'react'
import { TldsStyled } from './Tlds.styled'
import { obj_is_empty } from 'universal-common-scripts/functions/objects'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'
import { faInfo } from '@fortawesome/pro-solid-svg-icons'
import { faTimes, faAngleDown } from '@fortawesome/pro-regular-svg-icons'
import { H5 } from './DomainsResults.styled'
import Tip from '../../components/Tip'
import { Tooltip } from 'antd'

const DomainsResults = function(props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * Same as this.render():
   */
  let { tlds_user = {}, tlds_checked = {}, tlds_extra = [] } = that.props
  let tlds_user_arr = Object.keys(tlds_user)
  let tlds_checked_arr = Object.keys(tlds_checked)

  return (
    <TldsStyled className="TldsStyled">
      {/*
       * User extensions
       */}
      <div className="section">
        <div className={'heading'}>
          <H5 className="user">User's choice</H5>
        </div>
        {!!(tlds_user_arr && tlds_user_arr.length) && (
          <>
            {tlds_user_arr.map((tld, i) => (
              <div key={tld + 'ch' + i} className={'tld_line select'}>
                <span
                  className="tld clickable"
                  onClick={() => {
                    that.props.ui_actions.RX__focusSelectTld()
                  }}
                >
                  <b>.</b>
                  {tld}
                </span>
                <FA
                  className="clickable fa-angle-down"
                  icon={faAngleDown}
                  onClick={() => {
                    that.props.ui_actions.RX__focusSelectTld()
                  }}
                />
                {/*{tlds_user_arr.length !== 1 && (*/}
                {/*  <FA*/}
                {/*    icon={faTimes}*/}
                {/*    className="fa-times"*/}
                {/*    onClick={() => {*/}
                {/*      that.tld_uncheck(tld)*/}
                {/*    }}*/}
                {/*  />*/}
                {/*)}*/}
              </div>
            ))}
          </>
        )}
      </div>

      {/*
       * Checked
       */}
      {!!(tlds_checked_arr && tlds_checked_arr.length) && (
        <div className="section">
          <div className={'heading'}>
            <H5 className="checked">
              <span>Top&nbsp;</span>
              <span className="topN">
                {that.props.search_options.ext_suggestions_num}
                <FA className="fa-angle-down" icon={faAngleDown} />
                &nbsp;
              </span>
              <span className="hide-small">&nbsp;suggestions</span>
            </H5>
          </div>
          {tlds_checked_arr.map((tld, i) => (
            <div key={tld + 'ch' + i} className={'tld_line checked'}>
              <span className="tld">
                <b>.</b>
                {tld}
              </span>
              {!(i === 0 && obj_is_empty(tlds_user_arr)) && (
                <FA
                  icon={faTimes}
                  className="fa-times"
                  onClick={() => {
                    that.tld_uncheck(tld)
                  }}
                />
              )}
            </div>
          ))}

          {/*
           * Unused
           */}
          {tlds_extra && tlds_extra.map && (
            <div className="section">
              <div className={'heading'}>
                <H5 className="unchecked">
                  <span>Other</span>
                  <span className="show-small">s</span>
                  <span className="hide-small">&nbsp;suggestions&nbsp;</span>
                </H5>
                <div className="info">
                  learn more &thinsp;
                  <FA icon={faInfo} />
                </div>
              </div>
              {tlds_extra.map((tld, i) => (
                <div key={tld + 'ch' + i} className={'tld_line unchecked'}>
                  <span className="tld">
                    <b>.</b>
                    {tld}
                  </span>
                </div>
              ))}
            </div>
          )}
        </div>
      )}
    </TldsStyled>
  )
}

export default DomainsResults
